        mounted () {
            this.initData();
            this.windowHeight = window.innerHeight;
            // 
//            window.addEventListener('resize', this.watchHei, false);
        },
        beforeDestroy () {
//            window.removeEventListener('resize', this.watchHei, false);
        },
        watch: { //    watchdeep=true
            // showMeDOMnextTick
            showMe: function (value) { // valueshowMe
                if (value) {
                    this.$nextTick(() => {
                        this.init();
                    });
                }
            },
            //showLoadingnextTick
            showLoading: function (value){
                if (!value) {
                    this.$nextTick(() => {
                        this.getListHeight();
                    })
                }
            },
        },
        methods: {
            initData () {
                this.getmenuList(this.citySn);
                this.getCitySpots('110100');
                this.showLoading = false
            },
            getmenuList(val) {



              cityArea(val).then(res => {
                console.log(res);
                this.areaList = JSON.parse(JSON.stringify(res));
                console.log(this.areaList)
                this.area = res[0]
                //
                // this.citySenic = res.cityMap;
                // this.senicList = res.scenicspotList;

              })



            },
            getCitySpots (sn) {
              cityScenicspots(sn).then(res => {
                    console.log(res)
              })
            },
            toSearch() {
              this.$router.push('searchList');
            },
            //shopListTop
            getListHeight(){
                const listContainer = this.$refs.menuList;
                const listArr = Array.from(listContainer.children[0].children);
                listArr.forEach((item, index) => {
                    this.shopListTop[index] = item.offsetTop;
                });
                console.log(this.shopListTop);
                this.listenScroll(listContainer)
            },
            //scrollTop
            listenScroll(element){
                this.foodScroll = new BScroll(element, {
                    probeType: 3,
                    deceleration: 0.001,
                    bounce: false,
                    swipeTime: 2000,
                    click: true,
                });

                const wrapperMenu = new BScroll('#wrapper_menu', {
                    click: true,
                });

                const wrapMenuHeight = this.$refs.wrapperMenu.clientHeight;
                this.foodScroll.on('scroll', (pos) => {
                    if (!this.$refs.wrapperMenu) {
                        return
                    }
                    this.shopListTop.forEach((item, index) => {

                        if (this.menuIndexChange && Math.abs(Math.round(pos.y)) >= item) {
                            this.menuIndex = index;
                            const menuList=this.$refs.wrapperMenu.querySelectorAll('.active_ia');
                            // const el = menuList[0];
                            // wrapperMenu.scrollToElement(el, 800, 0, -(wrapMenuHeight/2 - 50));
                        }
                    })
                })
            },
            //
            chooseMenu(index){
                this.menuIndex = index;
                //menuIndexChangelistenScrollbug
                this.menuIndexChange = false;

                this.area = this.areaList[index];
                // console.log(area)
                // this.foodScroll.scrollTo(0, -this.shopListTop[index], 400);
                // this.foodScroll.on('scrollEnd', () => {
                //     this.menuIndexChange = true;
                // })
            }
            // resize
        }
